<template>
  <el-container class="field-props-container">
    <el-header v-if="!!!showingInDialog" class="field-props-header">
      [单选项]字段属性设置
    </el-header>
    <el-main class="field-props-pane">
      <el-form
        ref="editorForm"
        :model="fieldProps"
        :rules="rules"
        label-position="left"
        label-width="220px"
        size="mini"
        @submit.native.prevent
      >
        <el-form-item label="字段名称" prop="name">
          <el-input
            v-model="fieldProps.name"
            :disabled="fieldState !== 1"
          ></el-input>
        </el-form-item>
        <el-form-item label="显示名称" prop="label">
          <el-input v-model="fieldProps.label"></el-input>
        </el-form-item>
        <el-form-item label="字段长度（不超过6位）">
          <el-input-number
            v-model="fieldProps.field_length"
            :min="1"
            style="width: 100%"
          ></el-input-number>
        </el-form-item>
        <!-- <el-form-item label="最小值">
          <el-input
            v-model.number="fieldProps.fieldViewModel.minValue"
            type="number"
            style="width: 100%"
          ></el-input>
        </el-form-item>-->
        <el-form-item v-show="fieldProps.dictortsql == 1" label="字典">
          <el-select
            v-model="fieldProps.data_code"
            filterable
            remote
            reserve-keyword
            placeholder="请输入关键词"
            :remote-method="getDtList"
            :loading="loading"
            style="width: 100%"
          >
            <el-option
              v-for="item in dtList"
              :key="item.id"
              :label="item.name_zh"
              :value="item.id+''"
            ></el-option>
            <el-button slot="prefix" type="text" icon="el-icon-refresh" @click="refresh"></el-button>
          </el-select>
        </el-form-item>
        <el-form-item v-show="fieldProps.dictortsql == 2" label="自定义SQL">
          <el-input
            v-model="fieldProps.field_sql"
            placeholder="请输入SQL"
            type="textarea"
            style="width: 100%"
          ></el-input>
        </el-form-item>

        <el-form-item label="是否允许空值">
          <el-radio-group v-model="fieldProps.is_null" style="float: right">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="允许修改字段">
          <el-radio-group v-model="fieldProps.is_edit" style="float: right">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>

        <hr style="border: 0; border-top: 1px dotted #cccccc" />
        <el-form-item>
          <el-button
            type="primary"
            size="medium"
            style="width: 120px"
            @click="saveField"
          >
            保存字段
          </el-button>
          <el-button v-if="!!showingInDialog" size="medium" @click="cancelSave">
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
// import {addOptionField, updateOptionField, getField, getOptionItems} from '@/api/system-manager'
import { Message } from "element-ui";
import { copyObj } from "@/utils/util";
import FieldState from "@/views/system/field-state-variables";
import {saveField, findDt, refdt} from "@/api";
import { fieldEditorMixin } from "@/views/system/field-editor/field-editor-mixin";

export default {
  name: "OptionWidgetEditor",
  mixins: [fieldEditorMixin],
  props: {
    entity: Object,
    fieldName: String,
    showingInDialog: Boolean,
    fieldState: {
      type: Number,
      default: FieldState.NEW,
    },
  },
  data() {
    return {
      loading: false,
      dtList: [],
      fieldProps: {
        data_code: "",
        default_show: 0,
        facttable_id: "",
        field_length: 255,
        field_sql: "",
        file_path: "",
        file_size: 0,
        image_path: "",
        is_edit: 1,
        is_mainfield: 0,
        is_null: 1,
        is_system: 0,
        label: "",
        name: "",
        num_length: 0,
        order_number: 0,
        table_name: "",
        type: 14,
        verifiable: "",
        dictortsql: 1,
      },

      optionItems: [],

      hoverIdx: -1,

      validators: [],
      rules: {
        name: { required: true, message: "请输入字段名", trigger: "blur" },
        label: {
          required: true,
          message: "请输入字段显示名称",
          trigger: "blur",
        },
      },
    };
  },
  mounted() {
    if (this.fieldState === FieldState.EDIT) {
      this.getFieldProps();
    }
  },
  methods: {
    async refresh(){
      const {state,msg}=await refdt()
      this.$message({type:state,message:msg})
    },
    async getDtList(name) {
      this.loading = true;
      const { t } = await findDt({ name: name.trim() });
      this.dtList = t;
      this.loading = false;
    },
    getFieldProps() {
      // getField(this.fieldName, this.entity)
      //   .then((res) => {
      //     if (res.error != null) {
      //       this.$message({ message: res.error, type: "error" });
      //       return;
      //     }
      //     this.readFieldProps(res.data);
      //   })
      //   .catch((res) => {
      //     this.$message({ message: res.message, type: "error" });
      //   });
    },

    readFieldProps(savedProps) {
      copyObj(this.fieldProps, savedProps);
      if (savedProps.entityCode) {
        this.fieldProps.entityCode = savedProps.entityCode;
      }
    },

    async saveField() {
      let validResult = true;
      this.fieldProps.table_name = this.entity.name;
      this.fieldProps.facttable_id = this.entity.id;
      this.fieldProps.verifiable = this.validators.join(",");
      this.$refs["editorForm"].validate((success) => {
        if (!success) {
          validResult = false;
          return false;
        }
      });
      if (!validResult) return;
      this.modifyOldField(this.fieldProps,this.fieldState);
    },

    cancelSave() {
      this.$emit("cancelSave");
    },

    validateOption(optionTxt, optionIdx) {
      let result = true;
      this.optionItems.forEach((item, idx) => {
        if (item.label === optionTxt && idx !== optionIdx)
          // return false  /* forEach中return语句只是终止当前循环进行下一次循环，不能直接跳出函数返回函数结果！！ */
          result = false;
      });
      return result;
    },

    getOptionMaxValue() {
      let maxValue = 0;
      this.optionItems.forEach((item) => {
        if (item.value > maxValue) {
          maxValue = item.value;
        }
      });
      return maxValue;
    },

    addOption() {
      this.$prompt("请输入选项名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern:
          /^[A-Za-z\u4e00-\u9fa5\d]+$/ /* 匹配由字母大小写、汉字或数字组成的字符串 */,
        inputErrorMessage: "输入不正确",
      })
        .then(({ value }) => {
          if (this.validateOption(value, -1)) {
            let newOptionValue = this.getOptionMaxValue() + 1;
            let newOption = {
              label: value,
              value: newOptionValue,
              saved: false,
            };
            this.optionItems.push(newOption);
            this.$nextTick(() => {
              console.log("Updated!");
            });
          } else {
            this.$message.warning("选项已存在");
          }
        })
        .catch(() => {
          Message.info("已取消");
        });
    },

    insertOption(optionIdx) {
      this.$prompt("请输入选项名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern:
          /^[A-Za-z\u4e00-\u9fa5\d]+$/ /* 匹配由字母大小写、汉字或数字组成的字符串 */,
        inputErrorMessage: "输入不正确",
      })
        .then(({ value }) => {
          if (this.validateOption(value, -1)) {
            let newOptionValue = this.getOptionMaxValue() + 1;
            let newOption = {
              label: value,
              value: newOptionValue,
              saved: false,
            };
            this.optionItems.splice(optionIdx + 1, 0, newOption);
            this.$nextTick(() => {
              console.log("Updated!");
            });
          } else {
            this.$message.warning("选项已存在");
          }
        })
        .catch(() => {
          Message.info("已取消");
        });
    },

    upOption(optionIdx) {
      if (optionIdx === 0) {
        this.$message.info("已到最上");
        return;
      }

      let tempOption = this.optionItems[optionIdx];
      this.$set(this.optionItems, optionIdx, this.optionItems[optionIdx - 1]);
      this.$set(this.optionItems, optionIdx - 1, tempOption);
    },

    downOption(optionIdx) {
      console.log(
        "length: " + this.optionItems.length + ", current idx: " + optionIdx
      );
      if (optionIdx === this.optionItems.length - 1) {
        this.$message.info("已到最下");
        return;
      }

      let tempOption = this.optionItems[optionIdx];
      this.$set(this.optionItems, optionIdx, this.optionItems[optionIdx + 1]);
      this.$set(this.optionItems, optionIdx + 1, tempOption);
    },

    editOption(optionIdx) {
      let oldOptionLabel = this.optionItems[optionIdx].label;
      this.$prompt("请修改选项名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputValue: oldOptionLabel,
        inputPattern:
          /^[A-Za-z\u4e00-\u9fa5\d]+$/ /* 匹配由字母大小写、汉字或数字组成的字符串 */,
        inputErrorMessage: "输入不正确",
      })
        .then(({ value }) => {
          if (this.validateOption(value, optionIdx)) {
            this.optionItems[optionIdx].label = value;
            this.$nextTick(() => {
              console.log("Updated!");
            });
          } else {
            this.$message.warning("选项已存在");
          }
        })
        .catch(() => {
          Message.info("已取消");
        });
    },

    deleteOption(optionIdx) {
      console.log(optionIdx);
      this.$confirm("确定删除该选项?", "提示")
        .then(() => {
          //TODO：后台需要检查改选项是否已被实体记录所引用！！
          this.optionItems.splice(optionIdx, 1);
          this.$message.info("选项已删除");
        })
        .catch(() => {
          this.$message.info("取消删除");
        });
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/styles/form-layout/field-editor-common.scss";

.box-card {
  font-size: 14px;

  ::v-deep .el-card__body {
    height: 220px;
    overflow: auto;
  }

  .option-item {
    float: right;
    height: 22px;
    line-height: 22px;
    position: relative;

    .option-item-insert {
      position: absolute;
      top: -8px;
      left: -100px;
    }

    .option-item-up {
      position: absolute;
      top: -6px;
      left: -86px;
    }

    .option-item-down {
      position: absolute;
      top: -7px;
      left: -66px;
    }

    .option-item-edit {
      position: absolute;
      top: -7px;
      left: -42px;
    }

    .option-item-delete {
      position: absolute;
      top: -8px;
      left: -18px;
    }
  }
}

.clear-fix:before,
.clear-fix:after {
  display: table;
  content: "";
}

.clear-fix:after {
  clear: both;
}
</style>
